import React from 'react';
import Layout from '../../components/Layout';
import Register from './Register';
import Breadcrumbs from 'components/Breadcrumbs';

function action() {
  return {
    chunks: ['register'],
    component: (
      <Layout>
        <Breadcrumbs
          list={[
            { to: '/', txt: 'Home' },
            { to: '/shop', txt: 'Shop' },
            { to: '/register', txt: 'Register' },
          ]}
        />
        <Register />
      </Layout>
    ),
  };
}

export default action;



